@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*{font-family:"Poppins",sans-serif;font-weight:400}h1{font-size:3.052rem}h2{font-size:1.953rem}small,sub,sup{font-size:.64rem}button{border:none;outline:none;cursor:pointer;background-color:#3c4657;color:#fff;padding:0px 16px;border-radius:4px}button:hover{transition:.3s;transform:scale(1.1, 1.1);box-shadow:0px 0px 8px 2px #00000060}*{margin:0;padding:0;box-sizing:border-box}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{font-family:"Poppins",sans-serif;font-weight:400}h1{font-size:3.052rem}h2{font-size:1.953rem}small,sub,sup{font-size:.64rem}body{background-color:#1b212c}.home-cilent{margin:16px 0}.home-cilent main{width:100%;display:flex;justify-content:center}.home-cilent main .main-con{width:1270px;padding:0 32px}
.nav-cilent{width:100%;height:60px;color:#fff;background-color:#1b212c;display:flex;justify-content:center;box-shadow:0px 0px 8px 2px #00000060}.nav-cilent .nav-con{width:1270px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:0 32px}.nav-cilent .nav-con .nav-logo{font-size:1.2rem;cursor:pointer}.nav-cilent .nav-con .nav-item-con{height:100%;display:flex;align-items:center}.nav-cilent .nav-con .nav-item-con .nav-cart{font-size:1.5rem;cursor:pointer;width:3rem;height:3rem;display:flex;flex-direction:row;justify-content:center;align-items:center;position:relative}.nav-cilent .nav-con .nav-item-con .nav-cart:hover{transition:.3s;background-color:#3c4657;box-shadow:0px 0px 8px 2px #00000060;border-radius:8px}.nav-cilent .nav-con .nav-item-con .nav-cart .cart-counter{position:absolute;top:10px;right:5px;width:15px;height:15px;display:flex;flex-direction:row;justify-content:center;align-items:center;background-color:#f64b29;border-radius:50%;font-size:.52rem}.nav-cilent .nav-con .nav-item-con .nav-account{margin-left:8px;display:flex;align-items:center;cursor:pointer}.nav-cilent .nav-con .nav-item-con .nav-account .account-name{margin:0px 8px;font-size:.8rem;display:flex;align-items:center}.nav-cilent .nav-con .nav-item-con .nav-account .account-name .fa{font-size:.64rem;color:#29fc29;margin-right:4px}.nav-cilent .nav-con .nav-item-con .nav-account .account-img{width:30px;height:30px;border-radius:50%;overflow:hidden;display:flex;justify-content:center}.nav-cilent .nav-con .nav-item-con .nav-account .account-img img{width:200%}
