@import '../../sass/variable';
.nav-cilent {
  width: 100%;
  height: 60px;
  color: white;
  background-color: $bg-color;
  display: flex;
  justify-content: center;
  box-shadow: 0px 0px 8px 2px #00000060;
  .nav-con {
    width: 1270px;
    @include flexRow(space-between, center);
    padding: 0 32px;
    .nav-logo {
      font-size: 1.2rem;
      cursor: pointer;
    }
    .nav-item-con {
      // background-color: red;
      height: 100%;
      display: flex;
      align-items: center;
      .nav-cart {
        font-size: 1.5rem;
        cursor: pointer;
        // background-color: red;
        width: 3rem;
        height: 3rem;
        @include flexRow(center,center);
        position: relative;
        &:hover {
          transition: .3s;
          background-color: $bg-con;
          box-shadow: $shadow;
          border-radius: 8px;
        }
        .cart-counter {
          position: absolute;
          top: 10px;
          right: 5px;
          width: 15px;
          height: 15px;
          @include flexRow(center,center);
          background-color: $primary;
          border-radius: 50%;
          font-size: .52rem;
        }
      }
      .nav-account {
        margin-left: 8px;
        display: flex;
        align-items: center;
        cursor: pointer;
        .account-name {
          margin: 0px 8px;
          font-size: .8rem;
          display: flex;
          align-items: center;
          .fa {
            font-size: .64rem;
            color: $online;
            margin-right: 4px;
          }
        }
        .account-img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          overflow: hidden;
          display: flex;
          justify-content: center;
          img {
            width: 200%;
          }
        }
      }
    }
  }
}