@import '../../sass/variable';

.cart-list-con {
  // background-color: $bg-con;
  display: flex;
  width: 100%;
  cursor: pointer;
  padding: 8px;
  &:hover {
    background-color: $bg-con;
  }
  .list-item-img {
    width: 20%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    img {
      width: 150%;
      height: auto;
    }
  }
  .list-item {
    margin-left: 8px;
    width: 80%;
    .item-name {
      font-size: .7rem;
    }
    .item-detail {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      .item-weight {
        font-size: .57rem;
      }
      .item-price {
        margin-top: -2px;
        font-size: .8rem;
        color: yellow;
      }
    }
  }
}