@import '../../sass/variable';

.card-product-con {
  background-color: $bg-color;
  width: 12.4rem;
  height: 15rem;
  margin: 0px 32px 32px 8px;
  overflow: hidden;
  box-shadow: 0px 0px 10px 3px #00000060;
  border-radius: 4px;
  cursor: pointer;
  &:hover {
    transition: .3s;
    transform: translateY(-8px);
  }
  .card-product-img {
    // background-color: red;
    width: 100%;
    height: 9.8rem;
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;
    img {
      width: 130%;
      height: auto;
    }
    .img-shades {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 60%;
      @include shades
    }
  }
  .card-product-body {
    padding: 4px 8px;
    // background-color: blue;
    .body-name {
      font-size: 1rem;
      // margin: 4px 0px;
    }
    .body-detail {
      @include flexRow(space-between, center);
      font-size: .64rem;
      margin-bottom: 8px;
      color: rgb(233, 233, 233);
    }
    .body-price {
      display: flex;
      align-items: center;
      // background-color: red;
      .real-price {
        margin-right: 4px;
      }
      .diskon-price {
        color: yellow;
        text-decoration: line-through;
      }
    }
  }
  &:hover .img-shades {
    transition: .3s;
    height: 20%;
  }
}