$bg-color: #1b212C;
$bg-color-1: #28303bf1;
$bg-con: #3c4657;
$primary: #f64b29;
$secondary: #ffd6ea;
$online: rgb(41, 252, 41);
$shadow: 0px 0px 8px 2px #00000060;


@mixin shades {
  background: rgb(27,33,44);
  background: linear-gradient(0deg, rgba(27,33,44,1) 0%, rgba(27,33,44,0) 100%); 
}

@mixin flexRow($justify, $align) {
  display: flex;
  flex-direction: row;
  justify-content: $justify;
  align-items: $align;
}

@mixin flexCol($align, $justify) {
  display: flex;
  flex-direction: column;
  justify-content: $justify;
  align-items: $align;
}