@import '../../sass/variable';
.cart-con {
  // background-color: red;
  width: 100%;
  height: 100%;
  background-color: $bg-color;
  color: white;
  box-shadow: 0px 0px 8px 2px #00000060;
  padding: 16px;
  font-size: .8rem;
  overflow: auto;
  .cart-head {
    font-size: 1.4rem;
    // background-color: blue;
    @include flexRow(space-between, center);
    margin-bottom: 16px;
    .head-text {
      font-size: 1.3rem;
    }
    .head-close {
      // background-color: red;
      .close-cart {
        display: block;
        padding: 0;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        @include flexRow(center, center);
      }
    } 
  }
  .cart-body {
    margin-bottom: 16px;
  }
  .cart-foot {
    // background-color: red;
    @include flexRow(center, center);
    a {
      padding: 8px 24px;
      background-color: transparent;
      cursor: pointer;
      border-radius: 5px;
      // text-decoration: underline;
      &:hover {
        transition: .2s;
        box-shadow: $shadow;
      }
    }
  }
}