@import '../../sass/variable';

.detail-product-con {
  // background-color: rgba(255, 0, 0, 0.295);
  width: 100%;
  height: 100%;
  @include flexRow(center, end);
  color: white;
  .detail-product {
    background-color: $bg-color-1;
    width: 70%;
    height: 100%;
    border-top-left-radius: 32px;
    border-top-right-radius: 32px;
    padding: 24px 32px;
    box-shadow: $shadow;
    position: relative;
    overflow: hidden;
    .detail-close-btn {
      position: absolute;
      top: 10px;
      right: 20px;
      font-size: 1.6rem;
      cursor: pointer;
      &:hover {
        transition: .3s;
        transform: rotate(60deg);
      }
    }
    .detail-header {
      margin-bottom: 8px;
    }
    .detail-body {
      display: flex;
      overflow: hidden;
      height: calc(100% - 24px);
      // background-color: red;
      .body-img {
        width: 30%;
        display: flex;
        justify-content: center;
        overflow: hidden;
        border-radius: 10px;
        img {
          width: 150%;
          height: auto;
        }
      }
      .body-text {
        width: 70%;
        padding: 0px 16px;
        overflow: auto;
        .body-info {
          // background-color: red;
          @include flexRow(space-between, start);
          flex-wrap: wrap;
          margin-bottom: 8px;
          .body-info-1 {
            // background-color: rosybrown;
            .body-name {
              font-size: 1.4rem;
            }
            .body-category {
              font-size: .64rem;
            }
          }
          .body-info-2 {
            // background-color: royalblue;
            text-align: end;
            .body-price {
              font-size: 1.6rem;
            }
            .body-discount {
              font-size: .64rem;
              text-decoration: line-through;
              color: yellow;
            }
          }
        }
        .body-desc {
          font-size: .8rem;
          margin-bottom: 8px;
        }
        .body-submit {
          button {
            padding: 8px 32px;
            // box-shadow: $shadow;
            float: right;
            &:hover {
              transform: unset;
            }
            &:active {
              transform: scale(1.1,1.1);
            }
          }
          .submit-add {
            background-color: $primary;
          }
          .submit-remove {
            background-color: $bg-color;
          }
        }
      }
    }
  }
}